<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row>
                <a-col v-if="comboList && comboList.length > 0" :span="24">
                    <a :href="`../dc/comboCount/export?startDate=${date}&endDate=${date}`" target="_blank" style="float:right; margin-bottom: 16px;">
                        <a-icon type="file-excel" />
                        <span>倒出数据</span>
                    </a>
                </a-col>
            </a-row>
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="false">
                        <span slot="cook" slot-scope="cook">
                           {{cook || '无'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`../common/download?fileName=${img}`" />
                        </span>
                    </a-table>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
    import moment from 'moment';    
    import { getTodayOrders } from '@/api/order';
    export default {
        data() {
            return {
                columns    : [{
                    title       : '菜名名称',
                    dataIndex   : 'goodsName'
                },{
                    title       : '厨师名称',
                    dataIndex   : 'cookName',
                    scopedSlots : { customRender: 'cook' }
                }/*,{
                    title       : '套餐图片',
                    dataIndex   : 'comboImg',
                    scopedSlots : { customRender : 'img' }
                }*/,{
                    title       : '下单数',
                    dataIndex   : 'cnt'
                }],
                comboList  : [],
                loading    : false,
                date       : null
            }
        },
        methods : {
            _queryOrders(date) {
                this.date = date;
                this.loading = true ;
                getTodayOrders(date)
                    .then(r => {
                        this.loading = false;
                        this.comboList = (r || []).sort((a,b) => a.count - b.count);
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询今日订单详情失败,原因:${err}`)
                    })
            }
        },
        created() {
            this._queryOrders(moment(Date.now()).format('YYYY-MM-DD'));
        }
    }
</script>
