<template>
    <a-table 
        rowKey="id"
        :columns="columns" 
        :dataSource="comboList" 
        :loading="loading" 
        :pagination="pagination"
        @change="_onPageChange">
        <span slot="score" slot-scope="score">{{score || '0'}}</span>
        <span slot="desc" slot-scope="desc">{{desc || '默认评价.'}}</span>
    </a-table>
    
</template>

<script>
    import { queryUserComments } from '@/api/combo';
    export default {
        props : {
            userId : {
                type     : [Number, String],
                required : true
            }
        },
        data() {
            return {
                columns       : [{
                    title       : '套餐名称',
                    dataIndex   : 'comboName'
                },{
                    title       : '套餐编码',
                    dataIndex   : 'comboCode'
                },{
                    title       : '评分',
                    dataIndex   : 'eveScore',
                    scopedSlots : { customRender: 'score' }
                },{
                    title     : '评价',
                    dataIndex : 'eveDesc',
                    scopedSlots : { customRender: 'desc' }
                },{
                    title     : '评价时间',
                    dataIndex : 'orderDate'
                }],
                comboList     : [],
                pagination    : {
                    pageSize : 10
                },
                loading      : false,
                pageNo       : 1
            }
        },
        methods : {
            _onPageChange({ current }) {
                this.pageNo = current;
                this._queryComboComments();
            },
            _queryComboComments(userId) {
                this.loading = true ;
                console.log(queryUserComments);
                queryUserComments(userId)
                    .then(r => {
                        this.loading = false;
                        this.comboList =  r['rows'] || [];
                        const pObj = { ...this.pagination};
                        pObj.total = r['total'] || 0;
                        this.pagination = pObj;
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询评价失败,原因:${err}`);
                    })
            }
        },

        created() {
            this._queryComboComments(this.userId);
        }
    }
</script>