<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16" class="ke-fs-mg-b">
                <a-col :span="8">
                    <a-input-group compact>
                        <a-select v-model="searchType" style="width:40%" @change="_search">
                            <a-select-option value="">全部</a-select-option>
                            <a-select-option value="1">商务套餐</a-select-option>
                            <a-select-option value="2">精美面食</a-select-option>
                            <a-select-option value="3">特色美食</a-select-option>
                        </a-select>
                        <a-input-search style="width:60%" v-model="searchName" placeholder="请输入套餐名称..." @search="_search" />
                    </a-input-group>
                </a-col>
                <a-col :span="8" :offset="8" class="ke-fs-ta-r">
                    <a-dropdown>
                        <a-menu slot="overlay" @click="_onMenuClick">
                            <a-menu-item key="1">新增商务简餐</a-menu-item>
                            <a-menu-item key="2">新增精美面食</a-menu-item>
                            <a-menu-item key="3">新增特色美食</a-menu-item>
                        </a-menu>
                        <a-button>
                            <a-icon type="plus" />
                            <span>新增</span>
                            <a-icon type="down" />
                        </a-button>
                    </a-dropdown>
                </a-col>
            </a-row>
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="pagination" 
                        @change="_onPageChange">
                         <span slot="type" slot-scope="type">
                           {{'1' == type ? '商务简餐' : '2' == type ? '精美面食' : '特色美食'}}
                        </span>
                        <template slot="status" slot-scope="status">
                            <a-tag v-if="1 == status" color="green">启用</a-tag>
                            <a-tag v-else color="red">禁用</a-tag>
                        </template>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`../common/download?fileName=${img}`" />
                        </span>
                        <div class="combo-goods" slot="goods" slot-scope="goods">
                            <span v-for="g in goods" :key="g.id">{{g.goodsName}}</span>
                        </div>
                        <div class="combo-action" slot="actions" slot-scope="id, record">
                            <a-button size="small" icon="edit" @click="_modifyCombo(id)" />
                            <a-popconfirm title="是否确认删除当前套餐?" @confirm="_removeComboById(id)">
                                <a-button type="danger" size="small" icon="delete" />
                            </a-popconfirm>
                            <a-button v-if="'2' == record.status" size="small" type="primary" @click="_updateComboStatus(id, '1')">启用</a-button>
                            <a-button v-else size="small" type="danger" @click="_updateComboStatus(id, '2')">禁用</a-button>
                        </div>
                    </a-table>
                </a-col>
            </a-row>
        </div>
        <a-modal title="编辑" :width="680" :visible="isShowEditor" :footer="null" @cancel="isShowEditor=false" destroyOnClose>
            <ComboEditor 
                :comboId="edtComboId" 
                :type="comboType" 
                @success="_onSaveComboSuccess"
                @fail="_onShowDialogFial" />
        </a-modal>
    </div>
</template>

<script>
    import { queryPageByName, updateComboStatus } from '@/api/combo';
    import ComboEditor from '../../components/combo-editor/ComboEditor';
    export default {
        components : {
            ComboEditor
        },
        data() {
            return {
                columns    : [{
                    title       : '套餐名称',
                    dataIndex   : 'comboName'
                },{
                    title       : '套餐编码',
                    dataIndex   : 'comboCode'
                },{
                    title       : '套餐类型A',
                    dataIndex   : 'comboType',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '套餐图片',
                    dataIndex   : 'comboImg',
                    scopedSlots : { customRender : 'img' }
                },{
                    title       : '包涵菜品',
                    dataIndex   : 'goods',
                    scopedSlots : { customRender : 'goods' }
                },{
                    title       : '套餐状态',
                    dataIndex   : 'status',
                    scopedSlots : { customRender: 'status' }
                },{
                    title       : '操作',
                    dataIndex   : 'id',
                    scopedSlots : { customRender: 'actions' }
                }],
                comboList  : [],
                pagination : {
                    pageSize : 10
                },
                searchType   : '',
                searchName   : '',
                loading      : false,
                isShowEditor : false,
                edtComboId   : null,
                comboType    : null,
                pageNo       : 1
            }
        },
        methods : {
            _search() {
                this.pageNo = 1;
                this._queryComboList();
            },

            _newCombo(type) {
                this.comboType = type;
                this.edtComboId = null;
                this.isShowEditor = true;
            },

            _modifyCombo(id) {
                this.edtComboId = id;
                this.comboType = null;
                this.isShowEditor = true;
            },

            _removeComboById(id) {
                console.log('disabled:' + id);
            },

            _onSaveComboSuccess() {
                this.isShowEditor = false;
                this.$message.success('保存成功!');
                this._queryComboList();
            },

            _onShowDialogFial(err) {
                this.isShowEditor = false;
                this.$message.error('编辑套餐失败,原因:' + err);
            },

            _onPageChange({ current }) {
                this.pageNo = current;
                this._queryComboList();
            },

            _onMenuClick({ key }) {
                this._newCombo(key);
            },

            _queryComboList() {
                this.loading = true;
                console.log(this.searchType, this.searchName);
                queryPageByName(this.searchName, this.searchType, this.pageNo, this.pagination.pageSize)
                    .then(r => {
                        this.loading = false;
                        this.comboList =  r['rows'] || [];
                        const pObj = { ...this.pagination};
                        pObj.total = r['total'] || 0;
                        this.pagination = pObj;
                        console.log(this.comboList);
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询套餐失败,原因:${err}`);
                    })
            },
            _updateComboStatus(comboId, status) {
                updateComboStatus(comboId, status)
                    .then(() => {
                        const index = this.comboList.findIndex(v => v.id == comboId);
                        if(index != -1) {
                            this.comboList[index].status = status;
                        }
                        this.$message.success('更新套餐状态成功!');
                    })
                    .catch(err => {
                        this.$message.error('更改套餐状态失败,原因:' + err);
                    })
            },
        },

        created() {
            this._queryComboList();
        }
    }
</script>