<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="user_id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="pagination">
                        <span slot="type" slot-scope="type">
                           {{'1' == type ? '商务简餐' : '2' == type ? '精美面食' : '特色美食'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`../common/download?fileName=${img}`" />
                        </span>
                        <span slot="name" slot-scope="name, record">
                            {{name && name.length > 0 ? name : `用户${record.user_id}`}}
                        </span>
                        <span slot="cnt" slot-scope="cnt">{{cnt}}次</span>
                        <a-button slot="action" slot-scope="action" size="small" type="primary" @click="_showComments(action)">详情</a-button>
                    </a-table>
                </a-col>
            </a-row>
        </div>
        <a-modal title="评价详情" :width="680" :visible="isShowEditor" :footer="null" @cancel="isShowEditor=false" destroyOnClose>
            <PersonComments :userId="userId" />
        </a-modal>
    </div>
</template>

<script>
    import PersonComments from '../../components/person-comments/PersonComments';
    import { queryUserCommentCnt } from '@/api/combo';
    export default {
        components : {
            PersonComments
        },
        data() {
            return {
                columns    : [{
                    title       : '用户名称',
                    dataIndex   : 'user_name',
                    scopedSlots : { customRender : 'name' }
                }/*,{
                    title       : '套餐编码',
                    dataIndex   : 'combo_code'
                },{
                    title       : '套餐类型A',
                    dataIndex   : 'combo_type',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '套餐图片',
                    dataIndex   : 'combo_img',
                    scopedSlots : { customRender : 'img' }
                }*/,{
                    title       : '评分次数',
                    dataIndex   : 'cnt',
                    scopedSlots : { customRender : 'cnt' }
                },{
                    title       : '操作',
                    dataIndex   : 'user_id',
                    scopedSlots : { customRender : 'action' }
                }],
                pagination    : {
                    pageSize : 10
                },
                pageNo     : 1,
                comboList  : [],
                loading    : false,
                isShowEditor : false,
                userId       : null
            }
        },
        methods : {
            _onPageChange({ current }) {
                this.pageNo = current;
                this._queryComboStar();
            },
            _queryComboStar() {
                this.loading = true ;
                queryUserCommentCnt(this.pageNo)
                    .then(r => {
                        this.loading = false;
                        this.comboList =  r['rows'] || [];
                        const pObj = { ...this.pagination};
                        pObj.total = r['total'] || 0;
                        this.pagination = pObj;
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询星级评分失败,原因:${err}`)
                    })
            },
            _showComments(id) {
                this.userId = id;
                this.isShowEditor = true;
            }
        },
        created() {
            this._queryComboStar();
        }
    }
</script>
