import { postJSON } from './http';

function getTodayOrders(startDate) {
    return postJSON('/dc/comboCount/list', { startDate, endDate : startDate});
}

function getHistoryOrders(startDate) {
    return postJSON('/dc/comboCount/list', {startDate, endDate : startDate});
}

export {
    getTodayOrders,
    getHistoryOrders

}