<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="false">
                        <span slot="type" slot-scope="type">
                           {{'1' == type ? '商务简餐' : '2' == type ? '精美面食' : '特色美食'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`../common/download?fileName=${img}`" />
                        </span>
                        <span slot="avg" slot-scope="avg" class="combo-star" :class="{'hight' : avg > 3}">
                            {{Math.floor(avg  * 10) / 10 }}
                        </span>
                        <p slot="cook" slot-scope="cook,record" class="combo-cook">
                            <template v-if="record.cook_name || record.cook_img">
                                <img :src="`../common/download?fileName=${cook}`" />
                                <span>{{ record.cook_name || '' }}</span>
                            </template>
                            <span v-else class="empty">无</span>
                        </p>
                        <div slot="goods" slot-scope="goods,record" class="combo-goods">
                            <template v-if="record.goods && record.goods.length > 0">
                                <span v-for="g in record.goods" :key="g">{{g}}</span>
                            </template>
                            <a-button v-else size="small" :loading="true === record.loading" @click="_loadComboGoods(goods,record)">查看</a-button>
                        </div>
                        <a-button slot-scope="idx,record" slot="action" size="small" type="primary" @click="_showCommboComments(record.id)">详情</a-button>
                    </a-table>
                </a-col>
            </a-row>
        </div>
        <a-modal title="评分详情" :width="680" :visible="isShowEditor" :footer="null" @cancel="isShowEditor=false" destroyOnClose>
            <ComboComments :comboId="comboId" />
        </a-modal>
    </div>
</template>

<script>
    import { queryComboStar, queryComboById } from '@/api/combo';
    import ComboComments from '../../components/combo-comments/ComboComments';
    export default {
        components : {
            ComboComments
        },
        data() {
            return {
                columns    : [{
                    title       : '套餐名称',
                    dataIndex   : 'combo_name'
                },/*{
                    title       : '套餐编码',
                    dataIndex   : 'combo_code'
                },*/{
                    title       : '套餐类型',
                    dataIndex   : 'combo_type',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '套餐图片',
                    dataIndex   : 'combo_img',
                    scopedSlots : { customRender : 'img' }
                },{
                    title       : '厨师',
                    dataIndex   : 'cook_img',
                    scopedSlots : { customRender : 'cook'}
                },{
                    title       : '平均评分',
                    dataIndex   : 'avg',
                    scopedSlots : { customRender : 'avg' }
                },{
                    title       : '评分人数',
                    dataIndex   : 'cnt'
                },{
                    title       : '菜品详情',
                    dataIndex   : 'id',
                    scopedSlots : { customRender : 'goods' }
                },{
                    title       : '查看详情',
                    dataIndex   : 'idx',
                    scopedSlots : { customRender : 'action'}
                }],
                comboList    : [],
                loading      : false,
                isShowEditor : false,
                comboId      : null
            }
        },
        methods : {
            _queryComboStar() {
                this.loading = true ;
                queryComboStar()
                    .then(r => {
                        this.loading = false;
                        this.comboList = (r || []).map(v => {
                            v.loading = false;
                            v.goods = [];
                            return v;
                        });
                        
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询星级评分失败,原因:${err}`)
                    })
            },
            _loadComboGoods(id, record) {
                record.loading = true ;
                queryComboById(id)
                    .then(r => {
                        record.loading = false;
                        record.goods = (r['goods'] || []).map(v => v.goodsName);
                    })
                    .catch(err => {
                        this.$message.error(`获取套餐菜品列表失败,原因:${err}`);
                    });
            },
            _showCommboComments(id) {
                this.comboId = id;
                this.isShowEditor = true ;
            }
        },
        created() {
            this._queryComboStar();
        }
    }
</script>
