<template>
    <a-form-model
        layout="horizontal"
        :model="combo"
        :rules="rules"
        :labelCol="{span: 6}"
        :wrapperCol="{span: 12}"
        ref="form">
        <a-spin :spinning="querying">
            <a-alert v-if="hasError" type="error" :message="errorMsg" class="ke-fs-mg-b" />
            <a-form-model-item label="套餐图片" style="margin-bottom:0px;">
                <a-input type="hidden" v-model="combo.comboImg" />
                <a-upload
                    name="file"
                    accept="image/*"
                    action="../common/upload"
                    listType="picture-card"
                    :show-upload-list="false"
                    @change="_onCommboImgUpload">
                    <img v-if="combo.comboImg && combo.comboImg.length > 0" 
                        :src="`../common/download?fileName=${combo.comboImg}`" alt="套餐封面" />
                    <div v-else>
                        <a-icon :type="uploading ? 'loading' : 'plus'" />
                        <div class="ant-upload-text">上传</div>
                    </div>
                </a-upload> 
            </a-form-model-item>
            <a-form-model-item label="套餐名称" prop="comboName" required>
                <a-input v-model="combo.comboName" placeholder="请输入套餐名称" />
            </a-form-model-item>
            <a-form-model-item label="套餐编码" prop="comboCode" required>
                <a-input v-model="combo.comboCode" placeholder="请输入套餐编码" />
            </a-form-model-item>
            <a-form-model-item label="套餐菜品" prop="goodsIds" ref="goodsIds" required>
                <a-input type="hidden" v-model="combo.goodsIds" />
                <a-select v-model="combo.goodsIds" placeholder="请选择主食">
                    <a-select-option v-for="g in pkGoods" :value="g.value" :key="`pkg_${g.value}`">{{g.label}}</a-select-option>
                </a-select>
                <a-select v-if="'1' == combo.comboType || '3' == combo.comboType" mode="multiple" v-model="otherGoods" placeholder="请选择">
                    <a-select-opt-group v-for="g in otGoods" :label="g.label" :key="`otg_${g.value}`">
                        <a-select-option v-for="o in g.goods" :key="`otg_goods_${o.value}`" :value="o.value">{{o.label}}</a-select-option>
                    </a-select-opt-group>
                </a-select>
                <!--
                <a-select v-if="'3' == combo.comboType" mode="multiple" v-model="tsGoods" placeholder="请选择" @change="_onTsGoodsChange">
                    <a-select-option v-for="g in pkGoods" :value="g.value" :key="`pkg_${g.value}`">{{g.label}}</a-select-option>
                </a-select>
                <template v-else>
                    <a-select v-model="combo.goodsIds" placeholder="请选择主食">
                        <a-select-option v-for="g in pkGoods" :value="g.value" :key="`pkg_${g.value}`">{{g.label}}</a-select-option>
                    </a-select>
                    <a-select v-if="'1' == combo.comboType" mode="multiple" v-model="otherGoods" placeholder="请选择">
                        <a-select-opt-group v-for="g in otGoods" :label="g.label" :key="`otg_${g.value}`">
                            <a-select-option v-for="o in g.goods" :key="`otg_goods_${o.value}`" :value="o.value">{{o.label}}</a-select-option>
                        </a-select-opt-group>
                    </a-select>
                </template>
                -->
            </a-form-model-item>
            <a-form-model-item label="套餐描述" prop="comboDesc">
                <a-textarea v-model="combo.comboDesc" placeholder="请输入套餐描述" :rows="3" allowClear />
            </a-form-model-item>
            <a-form-model-item label="厨师照片" prop="cookImg">
                <a-input type="hidden" v-model="combo.cookImg" />
                <a-upload
                    name="file"
                    accept="image/*"
                    action="../common/upload"
                    listType="picture-card"
                    :show-upload-list="false"
                    @change="_onCookImgUpload">
                    <img v-if="combo.cookImg && combo.cookImg.length > 0" 
                        :src="`../common/download?fileName=${combo.cookImg}`" alt="厨师照片" />
                    <div v-else>
                        <a-icon :type="uploading ? 'loading' : 'plus'" />
                        <div class="ant-upload-text">上传</div>
                    </div>
                </a-upload>
            </a-form-model-item>
            <a-form-model-item label="厨师名称" prop="cookName">
                <a-input v-model="combo.cookName" placeholder="请输入厨师名称" />
            </a-form-model-item>
            <a-form-model-item :wrapper-col="{ span: 12, offset: 6}">
                <a-button type="primary" :loading="loading" @click="_submitForm">保 存</a-button>
            </a-form-model-item>
        </a-spin>
    </a-form-model>
</template>

<script>
    import { saveGoods, queryComboById, queryComboGoods } from '@/api/combo';
    const COMBO_TYPE_NAMES = {
        '1' : '主菜',
        '2' : '辅菜',
        '3' : '小菜',
        '4' : '例汤',
        '5' : '面条',
        '6' : '特色美食'
    };
    export default {
        props : {
            comboId : {
                type    : Number,
                default : null
            },
            type    : {
                type    : String,
                default : '1'
            }
        },
        data(){
            return {
                rules : {
                    comboName : [
                        { required : true, message : '套餐名称不能为空!', trigger : 'blur' }
                    ],
                    comboCode : [
                        { required : true, message : '套餐编码不能为空!', trigger : 'blur' }
                    ],
                    goodsIds : [
                        { required : true, message : '套餐菜品不能为空!', trigger : ['change', 'blur'] }
                    ]
                },
                combo : {
                    id : null,
                    comboName : '',
                    comboCode : '',
                    comboDesc : '',
                    comboImg  : '',
                    comboType : '1',
                    cookImg   : '',
                    cookName  : '',
                    goodsIds  : '',
                    status    : 2
                },
                tsGoods    : [],
                otherGoods : [],
                loading   : false,
                querying  : false,
                uploading : false,
                hasError  : false,
                errorMsg  : '',
                pkGoods   : [],
                otGoods   : []
            }
        },
        methods : {

            _onTsGoodsChange(value) {
                this.combo.goodsIds = value.join(',');
                this.$refs.goodsIds.onFieldChange();
            },
            _submitForm() {
                this.$refs.form.validate(r => {
                    r && this._saveCombo();
                    return r;
                });
            },
            _saveCombo() {
                const 
                    goodsIds = [this.combo.goodsIds, ...this.otherGoods].join(','),
                    combo    = {...this.combo};
                delete combo['goodsIds'];

                this.loading = true;
                this.hasError = false;
                saveGoods(combo, goodsIds)
                    .then(() => {
                        this.loading = false;
                        this.$emit('success');
                    })
                    .catch(err => {
                        this.loading = false;
                        this.hasError = true;
                        this.errorMsg = `保存失败,原因:${err}`;
                    });

            },
            _loadCombo(id) {
                return new Promise((resolve, reject) => {
                    queryComboById(id)
                    .then(({ id, comboName, comboCode, comboDesc, comboImg, comboType, cookImg = '', cookName = '', status, goods }) => {
                        this.querying = false;
                        this.combo = {
                            id, comboName, comboCode, comboDesc, comboImg, comboType, cookImg, cookName, status
                        };
                        if(goods && goods.length > 0) {
                            if('1' == comboType) {
                                const pkGoods = goods.filter(v => '1' == v.goodsType);
                                this.combo.goodsIds = pkGoods.length > 0 ? pkGoods[0].id : '';
                                const otGoods = goods.filter(v => '1' != v.goodsType);
                                this.otherGoods = otGoods.map(v => v.id);
                            } else if('3' == comboType) {
                                /*
                                this.pkGoods = goods;
                                this.tsGoods = goods.map(v => v.id);
                                this.combo.goodsIds = this.tsGoods.join(',');*/
                                const pkGoods = goods.filter(v => '6' == v.goodsType);
                                this.combo.goodsIds = pkGoods.length > 0 ? pkGoods[0].id : '';
                                const otGoods = goods.filter(v => '6' != v.goodsType);
                                this.otherGoods = otGoods.map(v => v.id);
                            } else {
                                this.combo.goodsIds = goods[0].id;
                            }
                        }
                        resolve();
                    })
                    .catch(err => reject(err));
                })
            },
            _queryComboGoods(comboType){
                const 
                    isJc = '1' == comboType,
                    isTs = '3' == comboType;
                 queryComboGoods(comboType)
                    .then(r => {
                        const goodsList = (r['rows'] || []).reduce((obj, g) => {
                            if(!obj[g.goodsType]) {
                                obj[g.goodsType] = {
                                    label : COMBO_TYPE_NAMES[g.goodsType] || '未知',
                                    value : g.goodsType,
                                    goods : []
                                };
                            }
                            obj[g.goodsType].goods.push({
                                label : g.goodsName,
                                value : g.id
                            });
                            return obj;
                        },{});
                        const tmpPkGoods = goodsList[isJc ? '1' : isTs ? '6' : '5'] || {};
                        this.pkGoods = tmpPkGoods.goods || [];
                        if(isJc) {
                            delete goodsList['1'];
                            this.otGoods = Object.keys(goodsList).map(k => goodsList[k]);
                        } else if(isTs) {
                            delete goodsList['6'];
                            this.otGoods = Object.keys(goodsList).map(k => goodsList[k]);
                        }
                        this.querying = false;
                    })
                    .catch(err => this.$emit('fail', err));
            },
            _onCommboImgUpload(e) {
                const status = e.file.status;
                if('uploading' === status) {
                    this.uploading = true;
                } else if('done' === status) {
                    this.combo.comboImg = e.file.response.fileName;
                    this.uploading = false;
                } else if('error' === status) {
                    this.$message.error('上传图片失败!');
                }
            },

            _onCookImgUpload(e) {
                const status = e.file.status;
                if('uploading' === status) {
                    this.uploading = true;
                } else if('done' === status) {
                    this.combo.cookImg = e.file.response.fileName;
                    this.uploading = false;
                } else if('error' === status) {
                    this.$message.error('上传图片失败!');
                }
            },

            _initEditorOnUpdate(comboId) {
                this._loadCombo(comboId)
                    .then(() => this._queryComboGoods(this.combo.comboType))
                    .catch(err => this.$emit('fail', err));
            },

            _initEditorOnCreate(comboType) {
                this.combo.comboType = comboType;
                this._queryComboGoods(comboType);
            }
        },
        created() {
            this.querying = true;
            if(this.comboId) {
                this._initEditorOnUpdate(this.comboId);
            } else {
                this._initEditorOnCreate(this.type);
            }
        }
    }
</script>