<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16" class="ke-fs-mg-b">
                <a-col :span="8">
                    <!-- <a-range-picker :defaultValue="defaultValue" @change="_search" /> -->
                    <a-date-picker v-model="end" @change="_search" />
                </a-col>
                <a-col :span="16">
                   <a v-if="comboList.length > 0" :href="`../dc/comboCount/export?startDate=${endString}&endDate=${endString}`" target="_blank" style="float:right; margin-bottom: 16px;">
                        <a-icon type="file-excel" />
                        <span>倒出数据</span>
                    </a>
                </a-col>
            </a-row>
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="false">
                        <span slot="cook" slot-scope="cook">
                           {{cook || '无'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`../common/download?fileName=${img}`" />
                        </span>
                    </a-table>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
    import moment from 'moment';
    import { getHistoryOrders } from '@/api/order';
    export default {
        data() {
            return {
                columns    : [{
                    title       : '菜名名称',
                    dataIndex   : 'goodsName'
                },{
                    title       : '厨师名称',
                    dataIndex   : 'cookName',
                    scopedSlots : { customRender: 'cook' }
                }/*,{
                    title       : '套餐图片',
                    dataIndex   : 'comboImg',
                    scopedSlots : { customRender : 'img' }
                }*/,{
                    title       : '下单数',
                    dataIndex   : 'cnt'
                }],
                defaultValue : null,
                comboList    : [],
                loading      : false,
                pageNo       : 1,
                end          : null
            }
        },
        computed : {
            endString : {
                'get' : function() {
                    return this.end.format('YYYY-MM-DD')
                }
            }
        },
        methods : {
            _search(value) {
                this._queryOrders(value.format('YYYY-MM-DD'));
            },
            _queryOrders(start) {
                this.loading = true ;
                getHistoryOrders(start)
                    .then(r => {
                        this.loading = false;
                        this.comboList = (r || []).sort((a,b) => a.count - b.count);
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询今日订单详情失败,原因:${err}`)
                    })
            },
            _onPageChange({ current }){
                this.pageNo = current;
                this._queryOrders(this.end.format('YYYY-MM-DD'));
            }
        },
        created() {
            const now = Date.now(),
                  old = new Date(now - 1000 * 60 * 60 * 24),
                end = moment(old)/*.format('YYYY-MM-DD')*/;
            this.end = end;
            this._queryOrders(this.end.format('YYYY-MM-DD'));
        }
    }
</script>
