<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="false">
                        <span slot="index" slot-scope="index" class="combo-index" :class="{'good' : isGoodTop10}">{{index}}</span>
                        
                        <span slot="avg" slot-scope="avg" class="combo-star" :class="{'hight' : avg > 3}">
                            {{Math.floor(avg  * 10) / 10 }}
                        </span>
                    </a-table>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
    import { queryComboTopTen } from '@/api/combo';
    export default {
        data() {
            return {
                columns    : [{
                    title       : '排名',
                    dataIndex   : 'index',
                    scopedSlots : { customRender: 'index' }
                },{
                    title       : '菜品名称',
                    dataIndex   : 'goods_name'
                },{
                    title       : '菜品评分',
                    dataIndex   : 'avg',
                    scopedSlots : { customRender: 'avg' }
                }],
                comboList  : [],
                loading    : false
            }
        },

        watch : {
            '$route' : function() {
                this._queryComboTopTen(this.$router.currentRoute.query.type);
            }
        },
        methods : {
            _queryComboTopTen(type) {
                this.isGoodTop10 = '1' == type;
                this.loading = true;
                queryComboTopTen(type)
                    .then(r => {
                        this.loading = false;
                        const list = r || [];
                        this.comboList = list.map((v,i) => {
                            v['index'] = i+1;
                            return v;
                        });
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询Top10失败,原因:${err}`)
                    });
            }
        },

        created() {
            this._queryComboTopTen(this.$router.currentRoute.query.type);
        }
    }
</script>